import React from 'react';
import trash from './trash.svg'
import credentials from "./credentials";
import branches from "./branches";

const md5 = require('md5');

let finalResult = {};

class Post extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            image: this.props.data.image,
            deleted: false,
            branches: []
        };
        this.result = this.props.data;
        this.result.deleted = false;
    }

    componentDidMount() {
        this.setState({branches: this.getBranches()});
    }

    updateFinalResult = () => {
        finalResult[this.props.data.filename] = this.result;
    };

    getBranches = () => {
        let arr = [];
        let i;
        for (i = 0; i < Object.keys(branches).length; i ++) {
            if (this.props.data.hasOwnProperty('branch')) {
                if (Object.values(this.props.data.branch)[0] === branches[Object.keys(branches)[i]]) {
                    arr.push(<label onChange={(e) => {this.result.branch = {}; this.result.branch[e.target.value] = branches[e.target.value]; this.updateFinalResult()}} key={i}><input name={'branch' + this.props.data.filename} type={'radio'} value={Object.keys(branches)[i]} defaultChecked={true}/>{Object.keys(branches)[i].toString()}</label>)
                } else {
                    arr.push(<label onChange={(e) => {this.result.branch = {}; this.result.branch[e.target.value] = branches[e.target.value]; this.updateFinalResult()}} key={i}><input name={'branch' + this.props.data.filename} type={'radio'} value={Object.keys(branches)[i]}/>{Object.keys(branches)[i].toString()}</label>)
                }
            } else {
                arr.push(<label onChange={(e) => {this.result.branch = {}; this.result.branch[e.target.value] = branches[e.target.value]; this.updateFinalResult()}} key={i}><input name={'branch' + this.props.data.filename} type={'radio'} value={Object.keys(branches)[i]}/>{Object.keys(branches)[i].toString()}</label>)
            }
        }
        return arr;
    };

    render() {
        return (
            <div style={{
                display: 'block',
                padding: '20pt',
                margin: '5pt',
                backgroundColor: 'whitesmoke',
                borderRadius: '10pt',
                textAlign: 'center'
            }}>
                <h4 hidden={!this.state.deleted}>WILL BE DELETED</h4>
                <img style={{width: '20vw', border: 'thin black solid'}} src={this.state.image}/>
                <br/>
                Image:<br/>
                <input disabled={this.state.deleted} defaultValue={this.props.data.image} onChange={(e) => {
                    this.setState({image: e.target.value});
                    this.result.image = e.target.value;
                    this.updateFinalResult();
                }} style={{width: '20vw'}}/><br/>
                Name:<br/>
                <input onChange={(e) => {this.result.name = e.target.value; this.updateFinalResult()}} disabled={this.state.deleted} defaultValue={this.props.data.name} style={{width: '20vw'}}/><br/>
                City:<br/>
                <input onChange={(e) => {this.result.city = e.target.value; this.updateFinalResult()}} disabled={this.state.deleted} defaultValue={this.props.data.city} style={{width: '20vw'}}/><br/>
                Date:<br/>
                <input onChange={(e) => {this.result.date = e.target.value; this.updateFinalResult()}} disabled={this.state.deleted} defaultValue={this.props.data.date} style={{width: '20vw'}}/><br/>
                Area:<br/>
                <input onChange={(e) => {this.result.area = e.target.value; this.updateFinalResult()}} disabled={this.state.deleted} defaultValue={this.props.data.area} style={{width: '20vw'}}/><br/>
                Fee:<br/>
                <input onChange={(e) => {this.result.fee = e.target.value; this.updateFinalResult()}} disabled={this.state.deleted} defaultValue={this.props.data.fee} style={{width: '20vw'}}/><br/>
                Range:<br/>
                <input onChange={(e) => {this.result.range = e.target.value; this.updateFinalResult()}} disabled={this.state.deleted} defaultValue={this.props.data.range} style={{width: '20vw'}}/><br/>
                Music:<br/>
                <input onChange={(e) => {this.result.music = e.target.value; this.updateFinalResult()}} disabled={this.state.deleted} defaultValue={this.props.data.music} style={{width: '20vw'}}/><br/>
                Location:<br/>
                <input onChange={(e) => {this.result.location = e.target.value; this.updateFinalResult()}} disabled={this.state.deleted} defaultValue={this.props.data.location}
                       style={{width: '20vw'}}/><br/>
                Time:<br/>
                <input onChange={(e) => {this.result.time = e.target.value; this.updateFinalResult()}} disabled={this.state.deleted} defaultValue={this.props.data.time} style={{width: '20vw'}}/><br/>
                User:<br/>
                <input onChange={(e) => {this.result.user = e.target.value; this.updateFinalResult()}} disabled={this.state.deleted} defaultValue={this.props.data.user} style={{width: '20vw'}}/><br/>
                Menu:<br/>
                <input onChange={(e) => {this.result.menu = e.target.value; this.updateFinalResult()}} disabled={this.state.deleted} defaultValue={this.props.data.menu} style={{width: '20vw'}}/><br/>
                Description:<br/>
                <textarea onChange={(e) => {this.result.description = e.target.value; this.updateFinalResult();}} disabled={this.state.deleted} defaultValue={this.props.data.description}
                          style={{width: '20vw', resize: 'none'}}/><br/>
                          Branch:<br/>
                <div>{this.state.branches}</div>
                <img onClick={() => {
                    const newBool = !this.state.deleted;
                    this.setState({deleted: newBool});
                    this.result.deleted = newBool;
                    this.updateFinalResult();
                }} style={{width: '5vw', cursor: 'pointer'}} src={trash}/>
            </div>
        )
    }
}

class ControlPanel extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            posts: [],
            user: '',
            pass: '',
            response: ''
        }
    }

    componentDidMount() {
        let xhr = new XMLHttpRequest();
        xhr.addEventListener('load', () => {
            console.log(JSON.parse(xhr.responseText));
            this.setState({posts: JSON.parse(xhr.responseText)});
        });
        xhr.open('GET', 'https://cors-anywhere.herokuapp.com/http://157.230.242.35:7000/get');
        xhr.send();
    }

    authenticate = () => {
        let i;
        for (i = 0; i < credentials.length; i++) {
            if (credentials[i].user === this.state.user && credentials[i].pass === md5(this.state.pass)) {
                return true;
            }
        }
        return false;
    };
    postResult = () => {
        if (this.authenticate()) {
            let xhr = new XMLHttpRequest();
            xhr.addEventListener('load', () => {
                this.setState({response: xhr.responseText});
            });
            xhr.open('POST', 'https://cors-anywhere.herokuapp.com/http://157.230.242.35:7000/post');
            const fin = Object.values(finalResult);
            console.log(fin);
            xhr.send(JSON.stringify(fin));
        } else {
            this.setState({response: 'INVALID CREDENTIALS'})
        }
    };
    getChildren = () => {
        let children = [];
        let i;
        for (i = 0; i < this.state.posts.length; i++) {
            children.push(<Post key={this.state.posts[i].filename} data={this.state.posts[i]}/>)
        }
        return children;
    };

    render() {
        return (
            <div style={{textAlign: 'center'}}>
                <div style={{display: 'flex', flexFlow: 'row wrap', justifyContent: 'center', alignItems: 'center'}}>
                    {this.getChildren()}
                </div>
                <div style={{backgroundColor: 'white'}}>
                    <label>
                        Username:
                        <input style={{margin: '5pt'}} onChange={(e) => {this.setState({user: e.target.value})}}/>
                    </label>
                    <br/>
                    <label>
                        Password:
                        <input style={{margin: '5pt'}} type={'password'} onChange={(e) => {this.setState({pass: e.target.value})}}/>
                    </label>
                    <br/>
                    <button style={{margin: '10pt'}} onClick={this.postResult}>POST TO TELO BOT</button>
                    <br/>
                    {this.state.response}
                    <br/>
                </div>
            </div>
        )
    }
}

export default ControlPanel;
