import React from 'react';
import categories from "./categories";
import credentials from "./credentials";
import './App.css';
import ControlPanel from './Control'
import branches from "./branches";

const md5 = require('md5');

class InitialInput extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      input: '',
      result: '',
      active: false,
        improves: false,
        improvesContent: {}
    };
  }
  componentDidMount() {
    this.checkStatus();
    this.getImproves();
  }
  checkStatus = () => {
    let xhr = new XMLHttpRequest();
    xhr.addEventListener('load', () => {
      if (xhr.responseText === 'ACTIVE') {
        this.setState({active: true})
      }
    });
    xhr.open('GET', 'https://cors-anywhere.herokuapp.com/http://157.230.242.35:8000/status');
    xhr.send();
  };
  getServerStatus = () => {
    if (this.state.active) {
      return(
          <div className={'server-active'}>
            Server is active
          </div>
      )
    }
    return (
        <div className={'server-inactive'}>
          Server is inactive
        </div>
    )
  };
  textChanged = (e) => {
    this.setState({input: e.target.value});
  };
  scrape = (e) => {
    let xhr = new XMLHttpRequest();
    xhr.addEventListener('load', () => {
      this.setState({result: xhr.responseText});
    });
    xhr.open('POST', 'https://cors-anywhere.herokuapp.com/http://157.230.242.35:8000/scrape');
    xhr.send(e.target.value);
  };
  getImproves = () => {
      let xhr = new XMLHttpRequest();
      xhr.addEventListener('load', () => {
          this.setState({improvesContent: JSON.parse(xhr.responseText)});
      });
      xhr.open('GET', 'https://cors-anywhere.herokuapp.com/http://157.230.242.35:7000/get-improves');
      xhr.send();
  };
  loadImproves = () => {
      let arr = [];
      let i;
      const keys = Object.keys(this.state.improvesContent);
      for (i = 0; i < keys.length; i ++) {
          arr.push(
              <div key={i} style={{width: '20vw', display: 'block', borderRadius: '5pt', border: 'thin black solid', margin: '5pt', padding: '5pt', backgroundColor: 'beige'}}>
                  {this.state.improvesContent[keys[i]]}
          </div>
          )
      }
      return arr;
  };
  render() {
    return(
        <div style={{textAlign: 'center'}}>
          <h1>Instagram Post Picker by eg0000r</h1>
          <h3>Compatible with scraper v1.0 and higher</h3>
          <h5>UPD: 4/4/20</h5>
          {this.getServerStatus()}
          <textarea onChange={this.textChanged} placeholder={'!2\neg0000r\n!1\npetrudenko'} style={{width: '90vw', height: '20vh', resize: 'none'}}/>
          <br/>
          <button onClick={this.scrape} value={this.state.input}>SCRAPE!</button>
          <br/>
          <textarea value={this.state.result} readOnly={true} placeholder={'Result'} style={{width: '90vw', height: '20vh', resize: 'none'}}/>
          <br/>
          <button onClick={this.props.fn} value={this.state.result}>LOAD</button>
            <div style={{display: 'flex', flexFlow: 'row wrap', justifyContent: 'center', alignItems: 'center', margin: '10pt'}}>{this.loadImproves()}</div>
        </div>
    )
  }
}

class Post extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      select: false
    }
  }
  selectPost = () => {
    this.setState({select: true});
    this.props.select(this.props.uid, {image: this.props.image, user: this.props.user, caption: this.props.caption});
  };
  removePost = () => {
    this.setState({select: false});
    this.props.remove(this.props.uid);
  };
  getButton = () => {
    if (this.state.select) {
      return(
          <div className={'select-active'} onClick={this.removePost}>-</div>
      )
    }
    return (
        <div className={'select'} onClick={this.selectPost}>+</div>
    )
  };
  render() {
    return (
        <div className={'post'}>
          <img className={'post'} src={this.props.image} alt={''}/>
          <p className={'post-user'}>{this.props.user}</p>
          <p className={'post-caption'}>{this.props.caption}</p>
          {this.getButton()}
        </div>
    )
  }
}

class PostAnalyzer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      category: {},
      name: '',
      location: '',
      date: '',
      description: '',
      music: '',
      fee: '',
      time: '',
      area: '',
      range: '$',
      city: 'Singapore',
        branch: {}
    }
  }
  callback = () => {
    const obj = {
      user: this.props.data.user,
      image: this.props.data.image,
      name: this.state.name,
      location: this.state.location,
      date: this.state.date,
      music: this.state.music,
      fee: this.state.fee,
      time: this.state.time,
      description: this.state.description,
      area: this.state.area,
      range: this.state.range,
      city: this.state.city,
      categories: this.state.category,
        menu: '',
        branch: this.state.branch
    };
    this.props.slot(this.props.uid, obj)
  };
  setBranch = (e) => {
    if (e.target.checked) {
      let obj = {};
      obj[e.target.value] = branches[e.target.value];
      this.setState({branch: obj});
    }
    this.callback();
  };
    setCategory = (e) => {
        if (e.target.checked) {
            let obj = this.state.category;
            obj[e.target.value] = categories[e.target.value];
            this.setState({category: obj});
        } else {
            let obj = this.state.category;
            delete obj[e.target.value];
            this.setState({category: obj});
        }
        this.callback();
    };
  loadCategories = () => {
    const keys = Object.keys(categories);
    let arr =[];
    let i;
    for (i = 0; i < keys.length; i ++) {
      arr.push(
          <label key={keys[i]}>
            <input onChange={this.setCategory} value={keys[i]} type={'checkbox'}/>
            {keys[i]}
          </label>
          )
    }
    return arr;
  };
    loadBranches = () => {
        const keys = Object.keys(branches);
        let arr =[];
        let i;
        for (i = 0; i < keys.length; i ++) {
            arr.push(
                <label key={keys[i]}>
                    <input onChange={this.setBranch} value={keys[i]} type={'radio'} name={'branch'}/>
                    {keys[i]}
                </label>
            )
        }
        return arr;
    };
  render() {
    return (
        <div className={'analyzer'}>
          <img className={'analyzer'} src={this.props.data.image} alt={''}/>
          <p><b>User:</b> {this.props.data.user}</p>
          <p><b>Caption:</b> {this.props.data.caption}</p>
          <p style={{color: 'red'}}>PLEASE, FORMAT DATE AND TIME LIKE SHOWN IN THE PLACEHOLDERS</p>
          <label style={{padding: '5pt'}}>
            <b>Event name: </b>
            <input onChange={(e) => {this.setState({name: e.target.value}); this.callback()}} placeholder={'TELO party'}/>
          </label>
          <label style={{padding: '5pt'}}>
            <b>Location: </b>
            <input onChange={(e) => {this.setState({location: e.target.value}); this.callback()}} placeholder={'123 Rockaway Blvd.'}/>
          </label>
          <label style={{padding: '5pt'}}>
            <b>Date: </b>
            <input onChange={(e) => {this.setState({date: e.target.value}); this.callback()}} placeholder={'dd.mm.yyyy'}/>
          </label>
          <br/>
          <label style={{padding: '5pt'}}>
            <b>Music: </b>
            <input onChange={(e) => {this.setState({music: e.target.value}); this.callback()}} placeholder={'Rock'}/>
          </label>
          <label style={{padding: '5pt'}}>
            <b>Entry fee: </b>
            <input onChange={(e) => {this.setState({fee: e.target.value}); this.callback()}} placeholder={'12'}/>
          </label>
          <label style={{padding: '5pt'}}>
            <b>Time: </b>
            <input onChange={(e) => {this.setState({time: e.target.value}); this.callback()}} placeholder={'23:00'}/>
          </label>
          <br/>
          <label style={{padding: '5pt'}}>
            <b>Area: </b>
            <input onChange={(e) => {this.setState({area: e.target.value}); this.callback()}} placeholder={'Novogireevo'}/>
          </label>
          <label style={{padding: '5pt'}}>
            <b>Price range: </b>
            <select onChange={(e) => {this.setState({range: e.target.value}); this.callback()}}>
              <option value={'$'}>$</option>
              <option value={'$$'}>$$</option>
              <option value={'$$$'}>$$$</option>
              <option value={'$$$$'}>$$$$</option>
            </select>
          </label>
          <label style={{padding: '5pt'}}>
            <b>City: </b>
            <select onChange={(e) => {this.setState({city: e.target.value}); this.callback()}}>
              <option value={'Singapore'}>Singapore</option>
              <option value={'Moscow'}>Moscow</option>
              <option value={'London'}>London</option>
              <option value={'Copenhagen'}>Copenhagen</option>
            </select>
          </label>
          <br/>
          <label style={{padding: '5pt'}}>
            <b>Description:</b>
            <br/>
            <textarea onChange={(e) => {this.setState({description: e.target.value}); this.callback()}} placeholder={'DJ Ewko. Unlimited bar. 18 to enter, 21 to drink. Girls free entry.'} style={{width: '80vw', resize: 'none'}}/>
          </label>
            <br/>
            <div>
                <b>Branch: </b>
                {this.loadBranches()}
            </div>
          <br/>
          <div>
            <b>Categories: </b>
            {this.loadCategories()}
          </div>
          <br/>
          <button onClick={this.callback}>Apply Changes</button>
        </div>
    )
  }
}

class Posts extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      response: '',
      user: '',
      pass: '',
      result: false,
      done: false,
      selected: {},
      processed: {}
    }
  }
  authenticate = () => {
    let i;
    for (i = 0; i < credentials.length; i ++) {
      if (credentials[i].user === this.state.user && credentials[i].pass === md5(this.state.pass)) {
        return true;
      }
    }
    return false;
  };
  postResult = () => {
    if (this.authenticate()) {
      let xhr = new XMLHttpRequest();
      xhr.addEventListener('load', () => {
        this.setState({response: xhr.responseText});
      });
      xhr.open('POST', 'https://cors-anywhere.herokuapp.com/http://157.230.242.35:8000/save');
      xhr.send(this.getResult());
    } else {
      this.setState({response: 'INVALID CREDENTIALS'})
    }
  };
  getResult = () => {
    let arr = [];
    let keys = Object.keys(this.state.processed);
    let i;
    for (i = 0; i < keys.length; i ++) {
      arr.push(this.state.processed[keys[i]]);
    }
    return JSON.stringify(arr);
  };
  processCallback = (k, v) => {
    let obj = this.state.processed;
    obj[k] = v;
    this.setState({processed: obj});
  };
  processPosts = () => {
    const keys = Object.keys(this.state.selected);
    let arr = [];
    let i;
    for (i = 0; i < keys.length; i ++) {
      arr.push(<PostAnalyzer key={keys[i]} uid={keys[i]} slot={this.processCallback} data={this.state.selected[keys[i]]}/>)
    }
    return arr;
  };
  selectPost = (k, v) => {
    let obj = this.state.selected;
    obj[k] = v;
    this.setState({selected: obj});
  };
  removePost = (k) => {
    let obj = this.state.selected;
    delete obj[k];
    this.setState({selected: obj});
  };
  loadPosts = () => {
    let out = [];
    let i;
    for (i = 0; i < this.props.data.length; i ++) {
      const user = this.props.data[i][0].user;
      let n;
      for (n = 1; n < this.props.data[i].length; n ++) {
        const image = this.props.data[i][n].media[0].url;
        const caption = this.props.data[i][n].text;
        out.push(<Post key={i.toString() + n.toString()} uid={i.toString() + n.toString()} select={this.selectPost} remove={this.removePost} image={image} user={user} caption={caption}/>)
      }
    }
    return out;
  };
  render() {
    if (!this.state.done) {
      return (
          <div style={{textAlign: 'center'}}>
            <div style={{display: 'flex', flexWrap: 'wrap'}}>
              {this.loadPosts()}
            </div>
            <button onClick={() => {this.setState({done: true})}} style={{marginLeft: 'auto', marginRight: 'auto', marginTop: '15pt', marginBottom: '15pt'}}>CONTINUE</button>
          </div>
      )
    } else if (!this.state.result) {
      return (
          <div style={{textAlign: 'center'}}>
            {this.processPosts()}
            <button onClick={() => {this.setState({result: true})}} style={{margin: '15pt'}}>GET JSON</button>
          </div>
      )
    }
    return (
        <div style={{textAlign: 'center'}}>
          <textarea defaultValue={this.getResult()} contentEditable={false} style={{resize: 'none', width: '90vw', height: '90vh'}}/>
          <br/>
          <div>
            <label>
              Username:
              <input style={{margin: '5pt'}} onChange={(e) => {this.setState({user: e.target.value})}}/>
            </label>
            <br/>
            <label>
              Password:
              <input style={{margin: '5pt'}} type={'password'} onChange={(e) => {this.setState({pass: e.target.value})}}/>
            </label>
            <br/>
            <button style={{margin: '10pt'}} onClick={this.postResult}>POST TO TELO BOT</button>
            <br/>
            {this.state.response}
            <br/>
          </div>
        </div>
    )
  }
}


class Content extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoaded: false,
      posts: {},
      showingControl: false,
    }
  }
  loadPosts = (e) => {
    this.setState({isLoaded: true, posts: JSON.parse(e.target.value)});
  };
  render() {
    if (!this.state.isLoaded) {
      if (!this.state.showingControl) {
        return (
            <div style={{textAlign: 'center'}}>
              <button onClick={() => {this.setState({showingControl: true})}}>GO TO CONTROL PANEL</button>
              <InitialInput fn={this.loadPosts}/>
            </div>
        )
      } else {
        return (
            <div style={{textAlign: 'center'}}>
              <button onClick={() => {this.setState({showingControl: false})}}>GO TO PICKER</button>
              <ControlPanel/>
            </div>
        )
      }
    }
    return (
        <Posts data={this.state.posts}/>
    )
  }
}

function App() {
  return (
    <div>
      <Content/>
    </div>
  );
}
